.lds-dual-ring {
    display: inline-block;
    width: 164px;
    height: 164px;
  }
  
  .lds-dual-ring:after {
    content: " ";
    display: block;
    width: 146px;
    height: 146px;
    margin: 1px;
    border-radius: 50%;
    border: 12px solid #7070eb;
    border-color: #06d6a2 transparent #7070eb transparent;
    animation: lds-dual-ring 1.2s linear infinite;
  }
  
  .loading-spinner__overlay {
    height: 99%;
    width: 99%;
    position: absolute;
    top: 0;
    left: 0;
    background: rgba(255, 255, 255, 0.85);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10000;
  }
  
  @keyframes lds-dual-ring {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }